import CityDetails from "./components/cityDetails/cityDetails";

function App() {
  return (
    <div className="App">
      <Routes>
        <Route path="/codicaWeather" element={<CityList />} />
        <Route path="/city/:id" element={<CityDetails />} />
      </Routes>
    </div>
  );
}

export default App;
